module.exports = "## 依赖技术 \r\n\r\n* [dojo](https://dojotoolkit.org/api/)\r\n* [Vue](https://cn.vuejs.org/v2/guide/)\r\n\r\n## 一个最简单的页面\r\n\r\n1. SimpleView.js\r\n\r\n```javascript\ndefine([\r\n    \"dojo/_base/declare\",\r\n    \"mobile/views/ViewBase\"\r\n], function (declare, ViewBase) {\r\n    return declare([ViewBase], {\r\n\r\n    });\r\n})\n```\r\n\r\n2. SimpleView.html\r\n\r\n```html\n<div>\r\n    <script type=\"dojo/require\">\r\n        lang: \"dojo/_base/lang\",\r\n        at: \"dojox/mvc/at\"\r\n    </script>\r\n    <div data-dojo-type=\"dojox/mobile/Heading\" data-dojo-props=\"label:'来肯云商'\">\r\n        <span class=\"backButton\" data-dojo-type=\"dojox/mobile/ToolBarButton\" data-dojo-props=\"onClick:lang.hitch(this,'onBackClick')\"></span>\r\n    </div>\r\n    <div data-dojo-type=\"dojox/mobile/ScrollableView\">\r\n        123\r\n    </div>\r\n</div>\r\n\n```\r\n\r\n3. 在 `mobile/config.json` views 属性下添加配置 **区分大小写**\r\n\r\n\r\n```json\r\n\"SimpleView\": {\r\n    \"template\": \"mobile/views/SimpleView.html\",\r\n    \"type\": \"mobile/views/SimpleView\"\r\n}\r\n\r\n```\r\n4. 再当前页面调用 `this.startView(viewName, params)`, `startView`, 在 `mobile/views/ViewBase` 中定义\r\n\r\n```javascript\r\nthis.startView(\"SimpleView\", {});\r\n```\r\n\r\n[mobile vue docs](mobilevuedocs)"

